.product-card-container {
  width: 100%;
  display: flex;
  height: 300px;
  align-items: center;
  position: relative;
  justify-content: center;
  
  .image {
    height: 70%;
    width: 70%;
    padding: .5rem;
    aspect-ratio: 3/4;
    object-fit: contain;
  }
  .buttons{
    position: absolute;
    top: 60%;
    display: grid;
    gap: 5px;
  }
  .inverted-add-to-cart {
    opacity: 0.7;
    display: none;

  }

  &:hover {
    .image {
      opacity: 0.8;
    }

    .inverted-add-to-cart {
      opacity: 0.85;
      display: flex;

    }

  }
  .product-card-details {
    width: 90%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    flex-direction: column;
    position: absolute;
    bottom: 5px;
    .name {
      font-weight: 300;


    }

    .price {
      color: #757575;
      gap: .2rem;
      display: flex;
      align-items: center;
    }
  }
}
@media (max-width: 768px) {
  .product-card-container {
    height: 310px;

    .image {
      height: 70%;
      width: 70%;
    }
    .buttons{
      position: absolute;
      top: 70%;
      display: grid;
      gap: 5px;
    }
    .product-card-details {
      font-size: 13px;
      height: 10%;
    }
  }
}
